img {
  height: auto;
  width: 100%;
  max-width: 300px;
}

.gridcard-card-container {
  padding: 20px !important;
}

.cardgrid-card {
  display: grid !important;
  grid-template-rows: 200px 130px;
  height: 350px;
  max-width: 250px !important;
  min-width: 250px !important;
  width: 100%;
}

.cardgrid-card-image-container {
  display: flex !important;
  justify-content: center !important;
}

.card-header {
  padding-top: 10px !important;
  padding-bottom: 0px !important;
  margin-bottom: 0px !important;
}

.content {
  justify-self: center;
}

.card-pricetag {
  padding: 10px;
  padding-left: 0;
}

.card-buttons {
  width: 100px;
  height: 35px;
}

.button-inner-text {
  font-size: 0.8em;
}

.card-grid-button-container-gallery {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.card-grid-button-container-singlebutton {
  display: flex;
  justify-content: center;
  align-items: baseline;
}
